body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'SEGA';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA'), url(/static/media/SEGA.82e09f78.TTF) ;
}

.card {
    width: 250px;
    margin: 10px;
    background-color: lightblue;
    border-radius: 10px;
    box-shadow: 0 0 4px 1px #000;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    cursor: pointer;
}
.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 0 10px 3px #222;
}
    .card-img {
        width: 200px;
        height: auto;
    }
    .card-info {
        text-align: center;
    }

.board {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    /* overflow-y: scroll;
    border: 1px solid #333;
    height: 500px; */
}

.app_box {
    width: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background: -webkit-linear-gradient(top left, rgb(7, 27, 82), rgb(0, 128, 128));
    background: linear-gradient(to bottom right, rgb(7, 27, 82), rgb(0, 128, 128));
}

.app_header {
    width: auto;
    margin-bottom: 40px;
}

.app_header > h1 {
    font-family: 'SEGA';
    color: #eee;
    font-weight: 300;
    font-size: 300%;
}

.search_box {
    width: 100%;
}
    .search_box input {
        padding: 10px;
        outline: 0;
        border: none;
        border-radius: 10px;
        width: 100%;
        text-align: center;
    }
    .search_box input::-webkit-input-placeholder {
        text-align: center;
    }
    .search_box input::-ms-input-placeholder {
        text-align: center;
    }
    .search_box input::placeholder {
        text-align: center;
    }

.scrollable {
    overflow-y: scroll;
    border: 2px solid #333;
    height: 400px;
}
